import { useState, useEffect } from "react";
import { Heart } from "lucide-react";
import { Button } from "@/components/ui/button";
import { useAuth } from "@/contexts/AuthContext";
import { useToast } from "@/hooks/use-toast";
import { supabase } from "@/integrations/supabase/client";
import i18n from "@/i18n/config";
import { useTranslation } from "react-i18next";

interface ActivityFavoriteButtonProps {
  activityId: string;
  initialSubscribed?: boolean;
}

export const ActivityFavoriteButton = ({ 
  activityId, 
  initialSubscribed = false 
}: ActivityFavoriteButtonProps) => {
  const { t } = useTranslation();
  const { user } = useAuth();
  const { toast } = useToast();
  const [isSubscribed, setIsSubscribed] = useState(initialSubscribed);
  const [isPending, setIsPending] = useState(false);

  // Sync with initial value when it changes
  useEffect(() => {
    setIsSubscribed(initialSubscribed);
  }, [initialSubscribed]);

  const handleToggle = async (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();

    if (!user) {
      toast({
        title: t("activities.signInToSubscribe") || "请先登录",
        description: t("activities.signInToSubscribeDesc") || "您需要登录才能订阅活动",
        variant: "destructive",
      });
      return;
    }

    setIsPending(true);
    
    // Optimistic update
    const wasSubscribed = isSubscribed;
    setIsSubscribed(!wasSubscribed);

    try {
      if (wasSubscribed) {
        const { error } = await supabase
          .from("subscriptions")
          .delete()
          .eq("activity_id", activityId)
          .eq("user_id", user.id);

        if (error) throw error;
        
        toast({
          title: i18n.t("toast.unsubscribed"),
          description: i18n.t("toast.unsubscribedDesc"),
        });
      } else {
        const { error } = await supabase
          .from("subscriptions")
          .insert({
            activity_id: activityId,
            user_id: user.id,
          });

        if (error) throw error;
        
        toast({
          title: i18n.t("toast.subscribed"),
          description: i18n.t("toast.subscribedDesc"),
        });
      }
    } catch (error) {
      // Rollback on error
      setIsSubscribed(wasSubscribed);
      toast({
        title: i18n.t("common.error"),
        description: i18n.t("toast.subscriptionFailed"),
        variant: "destructive",
      });
    } finally {
      setIsPending(false);
    }
  };

  return (
    <Button
      variant={isSubscribed ? "default" : "outline"}
      size="icon"
      onClick={handleToggle}
      disabled={isPending}
      title={isSubscribed ? t("activities.favorited") : t("activities.favorite")}
      className="transition-all duration-200"
    >
      <Heart 
        className={`w-4 h-4 transition-all duration-200 ${
          isSubscribed ? "fill-current" : ""
        }`} 
      />
    </Button>
  );
};
